/*  Third Article Style
------------------------------------------------------------------------------------------*/
.history-article {
    background: url("../images/02.jpg") no-repeat center center / cover;

    h1 {
        margin: 0;
        color: #414f6b;
    }

    .text.wrap {
        height: 70vh;
        display: grid;
        grid-template-columns: repeat(2, auto);
        grid-template-rows: auto;
        padding: 1em 2em 0 2em;

        #text-holder {
            grid-column: 1/3;
            grid-row: 1/2;
            padding-top: 5em;
        }
    }


    .img-btn-contianer {
   //     height: 26.6vh;
        display: grid;
        grid-template-columns: repeat(2, auto);
        grid-template-rows: repeat(2, auto);
        padding: 2em 2em 0 2em;
        background-color: hsla(220, 24%, 34%, 0.8);
    }

    #img-holder {
        grid-column: 1/3;
        grid-row: 1/2;

        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 0 2em 0 2em;

        figure {
            // width: ;
            flex: 0 0 15.8125em;
            margin: 0;
        }

        img {
            display: block;
            margin: 0;
            width: 100%;
        }
    }

    .btn-holder {
        grid-column: 1/3;
        grid-row: 2/3;

        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        height: 1.3em;
        margin-top: 1em;

        label {
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            margin-right: 10px;
            background-color: #414f6b;
        }

        label:hover {
            cursor: pointer;
        }

        label:nth-of-type(1) {
            background-color: #fff;
        }
    }
}

