/*  Sixth Article Style
------------------------------------------------------------------------------------------*/
.sixth-article-holder {
    background: url("../images/05.jpg") no-repeat center center /cover;
}

.sixth-article-content {
    height: 100vh;
    padding: 10em 2em 0 2em;
}

#content-holder {
    width: 30em;
    display: grid;
    grid-template-columns: repeat(2, 10em);
    grid-template-rows: repeat(6, auto);
    background-color: hsla(0, 0%, 100%, 0.5);
    padding: 2em 2em 0 2em;
    
    h2 {
        grid-column: 1/3;
        grid-row: 1/2;
        margin: 0;
        color: #414f6b;
    }

    span:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 2/3;
    }

    span:nth-of-type(2) {
        grid-column: 2/3;
        grid-row: 2/3;
    }

    span:nth-of-type(3) {
        grid-column: 1/2;
        grid-row: 3/4;
    }

    span:nth-of-type(4) {
        grid-column: 2/3;
        grid-row: 3/4;
    }

    span:nth-of-type(5) {
        grid-column: 1/2;
        grid-row: 4/5;
    }

    span:nth-of-type(6) {
        grid-column: 2/3;
        grid-row: 4/5;
    }

    span:nth-of-type(7) {
        grid-column: 1/2;
        grid-row: 6/7;
    }

    span:nth-of-type(8) {
        grid-column: 2/3;
        grid-row: 6/7;
    }
}