/*  Footer Style
------------------------------------------------------------------------------------------*/
.ftr-holder {
    background-color: #414f6b;
}

.fter {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: auto;
    padding: 2em 2em 0 2em;

    div:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 1/2;
        display: flex;
        flex-wrap: wrap;
    }

    div:nth-of-type(2) {
        grid-column: 2/3;
        grid-row: 1/2;
        text-align: right;
    }

    figure {
        width: 2.6875em;
        margin: 0;
    }

    img {
        display: block;
        width: 100%;
        margin: 0;
        border-radius: 50%;
    }

    #fter-text {
        margin-left: 1em;
        align-self: center;
    }
}