/*  Second Article Style
------------------------------------------------------------------------------------------*/
.nav-options.wrap {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: auto;
    padding: 0.5em 2em 0.5em 2em;


    div:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 1/2;
        display: flex;
        flex-wrap: wrap;

        div {
            margin-left: 1em;
            padding-top: 1em;
        }
    }

    div:nth-of-type(2) {
        grid-column: 2/3;
        grid-row: 1/2;

        ul {
            margin: 0;
            padding: 0;
            padding-top: 1em;
            display: flex;
            flex-wrap: wrap;
            justify-content: right;
        }

        li {
            flex: 0 0 auto;
            list-style: none;
        }

        li:nth-of-type(1) {
            margin-right: 5em;
        }
    }

    figure { 
        width: 2.6875em;
        margin: 0;
        
        img {
            display: block;
            width: 100%;
            margin: 0;
            border-radius: 50%;
        }
    }
}