/*  Fifth Article Style
------------------------------------------------------------------------------------------*/
.menu-article {
    background-color: #414f6b;

    ul {
        width: 25em;
        margin: 0;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    li {
        flex: 1 0 0;
        list-style: none;
    }

    a {
        display: flex;
        justify-content: center;
        text-decoration: none;
        color: #b0b4be;
        font-size: 20px;
        font-weight: bold;
        padding: 5px;
    }

    a:hover {
        color:  #414f6b;
        text-decoration: underline;
        background-color: #b0b4be;
    }
}