/*  First Article Style
------------------------------------------------------------------------------------------*/
.arti {
    height: 100vh;
    background: url("../images/08.jpg") no-repeat center center / cover;

}

header {
    figure {
        width: 2.6875em;
        margin: 0;
    }

    img {
        display: block;
        width: 100%;
        margin: 0;
        border-radius: 50%;
    }

    ul {
        margin: 0;
        margin-top: 1em;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: right;

        li {
            flex: 0 0 auto;
            list-style: none;
            border-bottom: 2px solid #fff;
        }

        li:nth-of-type(1) {
            margin-right: 5em;
        }

        a {
            display: flex;
            text-decoration: none;
            color: #fff;
        }
    }
}

.hder {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: auto;
    padding: 1em 2em 0 2em;

    div:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 1/2;
    }
    
    div:nth-of-type(2) {
        grid-column: 2/3;
        grid-row: 1/2;
    }
}