/*  Forth Article Style
------------------------------------------------------------------------------------------*/
.climb-arti {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: auto;
    padding: 2em 2em 0 2em;

    div {
        grid-column: 1/3;
        grid-row: 1/2;

        h1 {
            margin: 0;
            color: #414f6b;
        }
    }
}